/* Global Variables */
:root {

	/* Tile Dimensions */
	--carouselTile-spacing: 10px;
	--carouselTile-width:   280px;
	--carouselTile-height:  calc(var(--carouselTile-width) / (16 / 9));

	/* Growth Factor */
	--carousel-growth-factor: 1.2;

	/* Fade to Opacity */
	--carousel-fade-opacity:   0.25;
	--carousel-normal-opacity: 1;

	/* Automatic Offsets - DO NOT ALTER */
	--carousel-offset-left:  calc(-1 * (var(--carouselTile-width) * (var(--carousel-growth-factor) - 1) / 2));
	--carousel-offset-right: calc(var(--carouselTile-width) * (var(--carousel-growth-factor) - 1));

	/* Transition Speeds */
	--carousel-transition-1: 1s;
	--carousel-transition-2: 0.5s;
	--carousel-transition-3: 0.3s;

}

.SliderContainer_overlay__3onHY {
	position: fixed;
	width: 100vw;
	height: 100vh;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.3);
    z-index: 0;
}

/* Carousel Container */
.SliderContainer_carousel__2utbC {
	margin: 0;
	box-sizing: border-box;
	width: 100%;
	/*height: 225px; /*60%*/
	max-height: 500px;
	overflow-x: auto;
	overflow-y: hidden;
}

::-webkit-scrollbar{width:2px;height:2px;}

/* Carousel Row */
.SliderContainer_carouselRow__2kasp {
	white-space: nowrap;
	margin-top: calc((var(--carouselTile-height) * (var(--carousel-growth-factor) - 1)) / 2);
	margin-bottom: calc((var(--carouselTile-height) * (var(--carousel-growth-factor) - 1)) / 2);
	margin-left: calc((var(--carouselTile-width) * (var(--carousel-growth-factor) - 1)) / 2);
	margin-right: calc((var(--carouselTile-width) * (var(--carousel-growth-factor) - 1)) / 2);
	transition: var(--carousel-transition-2);
}

/* Content Tile */
.SliderContainer_carouselTile___-Eqb {
	position: relative;
	display: inline-block;
	/* width: 150px;/* var(--carouselTile-width);*/
	/* height: 225px;/* var(--carouselTile-height);*/
	margin-right: var(--carouselTile-spacing);
	transition: var(--carousel-transition-2);
	-webkit-transform-origin: center left;
	transform-origin: center left;
	cursor: pointer;
	cursor: hand;
}

/* Add Extra Margin to Last Carousel Tile */
.SliderContainer_carouselTile___-Eqb:last-of-type {
	margin-right: calc(var(--carouselTile-width)/2);
}


/* Ensure All Elements Inside Tile are Block */
.SliderContainer_carouselTile___-Eqb * {
	display: block;
}

/* Image style */
.SliderContainer_img__s-bPj {
	width: 100%;
}

@media screen and (min-width: 600px) {
	/* Carousel Row on Hover */
	.SliderContainer_carouselRow__2kasp:hover {
		-webkit-transform: translate3d(var(--carousel-offset-left), 0, 0);
		transform: translate3d(var(--carousel-offset-left), 0, 0);
	}

	/* Content Tile on Carousel Row Hover */
	.SliderContainer_carouselRow__2kasp:hover .SliderContainer_carouselTile___-Eqb {
		opacity: var(--carousel-fade-opacity);
	}

	/* Content Tile on Hover on Carousel Row Hover */
	.SliderContainer_carouselRow__2kasp:hover .SliderContainer_carouselTile___-Eqb:hover {
		-webkit-transform: scale(var(--carousel-growth-factor));
		transform: scale(var(--carousel-growth-factor));
		opacity: var(--carousel-normal-opacity);
	}

	/* Content Tile on Hover */
	.SliderContainer_carouselTile___-Eqb:hover ~ .SliderContainer_carouselTile___-Eqb {
		-webkit-transform: translate3d(var(--carousel-offset-right), 0, 0);
		transform: translate3d(var(--carousel-offset-right), 0, 0);
	}
}

